import React, { useState, useEffect } from 'react';

import ImpactPaths from './ImpactPaths.js';
import ImpactMap from './ImpactMap.js';
import ImpactGraph from './ImpactGraph.js';

const showGraph = false; // if True, impact graph will be rendered

// function used to build graph objects
function addEdge(G,start,end,attr){
    if (!G.nodes){ G.nodes = {} } // ensure node dict is defined
    if (!G.edges){ G.edges = {} } // ensure edge dict is defined
    if (!G.pred){ G.pred = {} } // ensure predecessors dict is defined
    if (!G.succ){ G.succ = {} } // ensure successors dict is defined

    // add nodes and edges
    if (!G.nodes[start]){ G.nodes[start] = {} }
    if (!G.nodes[end]){ G.nodes[end] = {} }
    if (!G.edges[start]){ G.edges[start] = {} }
    G.edges[start][end] = attr // add edge

    // add nodes to predeccessors and successors maps
    if (!G.succ[start]){ G.succ[start] = new Set([]) }
    if (!G.succ[end]){ G.succ[end] = new Set([]) }
    if (!G.pred[start]){ G.pred[start] = new Set([]) }
    if (!G.pred[end]){ G.pred[end] = new Set([]) }
    G.succ[start].add(end);
    G.pred[end].add(start);
}

// build a graph from the specified edges 
function buildGraph( edges ){
    const G = {};
    for (const edge of edges) {
        const [start, end, attr] = edge; // unpack
        addEdge(G, start, end, attr);
    }
    return G;
}

// parse an equation stored as nested lists
function getVal( v, variables ) {
    if (typeof v === 'number') {
        return v;
    } else if (typeof v === 'string'){
        if (variables[v] !== null){
            return variables[v]; // lookup value of referenced variable
        } else {
            return NaN; // replace "null" with NaN
        }
    } else if (typeof v === 'object'){
        const prod = v.map( (v2) => {
            return getVal(v2, variables );
        } );
        return prod.reduce((a,v)=>a*v, 1);
    }
}

// compute partial derivative (impact tendency)
// of a single edge
function computePartial( G, start, end, variables ){
    //const bits = ["a","b","c"]; // TODO -- make this a bit more complex
    const vals = G.edges[start][end]['tend'].map( (v) => {return getVal(v, variables) }); // get components in tendency (handling all multiplications)
    const dEdI = vals.reduce((a,v)=>a+v, 0); // sum tendency components

    // store edge gradient in graph for future use
    G.edges[start][end].dEdI = dEdI;
    return dEdI;
}

// Compute tendency recursively
function computeTendency(G, n, seed, variables ) {

    // we have reached a seed node so impact tendency is known
    // (so store it and return).
    if (seed[n]) {
        variables[n] = seed[n];
        return seed[n];
    }

    // node impact tendency already computed; just return it :-) 
    //if (n in variables) return variables[n];
    
    // accumulate impact tendency
    let output = 0;
    for (const p of G.pred[n]) { // recursively loop through predecessor nodes
      
        // get tendency of this node so far
        const tendency = computeTendency(G, p, seed, variables );
        const dEdI = computePartial(G, p, n, variables ); // tendency of this edge
        
        // accumulate
        //if (!isNaN(dEdI)){
        output += dEdI * tendency
        //}
    }

    // store and return
    G.nodes[n].tendency = output;
    variables[n] = output;
    return output;
}

// compute all unique paths from the specified start node
//  (this computes all paths from start to any target node)
function getUniquePaths(G, start, paths={}, chain=[] ){
    if (!(start in paths)){
        paths[start] = [];
    }
    chain.push(start);
    paths[start].push(chain);
    for (const p of G.succ[start]) {
        getUniquePaths(G, p, paths, chain.slice() );
    }
    return paths;
}

// get impact pathways
function getPathways( G, start, target ){

    // get paths and compute their individual impacts
    const paths = getUniquePaths( G, start )[target];
    const path_t = paths.map((path) => // (multiply everything togther to accumulate)
        path.slice(1).reduce((acc, node, i) => acc * G.edges[ path[i] ][ node ].dEdI, 1)
    );
    
    // loop through and store in output object
    // N.B. for convenience this separates pathways with
    // positive, negative and uncomputed (due to NaNs) tendencies
    const out = { positive:[], negative:[], unknown:[] };
    path_t.forEach((tend, ix) => {

        // convert path to a simpler (list-based) form
        const path = paths[ix].slice(0,paths[ix].length-1).map( (p,i) => { 
            const s = paths[ix][i];
            const e = paths[ix][i+1];
            return [s, e, G.edges[s][e].dEdI, G.nodes[s].tendency ];
        });

        // store
        const impact = { tendency: tend, path: path };
        if (isNaN(tend)) { out.unknown.push(impact); }
        else if (tend > 0) { out.positive.push(impact); }
        else { out.negative.push(impact); }
      });
    return out;
}

// compute impact given the specified graph, features and variables.
// note that variables should also include spatially varied ones
// (i.e. this should be run for each spatial object separately,
//  updating variables accordingly).
function computeImpact(G, variables, seed_node, target_node ){
    
    // compute impact tendency
    const seed = {}
    seed[seed_node] = 1;
    const tendency = computeTendency(G,
                    target_node, // start node of backward recursion
                    seed, // seed "increase" that triggers effect
                    variables, // current properties used to compute edge gradients. Node values will be put here too.
                     );

    // get impact pathways for plotting later
    const pathways = getPathways( G, seed_node, target_node );
    pathways.total = tendency; // store total impact

    // return
    return {tendency: tendency, pathways: pathways}
}

function Model({ data }) {

    // define state variables
    const [selectedGID, setSelectedGID] = useState(null); // selected geometry ID
    const [variables, setVariables] = useState(data._variableDefaults); // impact model variables
    const [mapAttrs, setMapAttrs] = useState( null ); // geometric properties to be exposed to map app
    const [pathways, setPathways] = useState( null ); // impact pathways to be exposed to pathway app 
    const [mapKey, setMapKey] = useState(0); // key used to trigger map redraw
    const [graph, setGraph] = useState( null );

    // todo; don't set these explicitely!?
    const seed_node = 'properties/Mining'; 
    const target_node = 'properties/SharedValue';
    if (!mapAttrs){
        console.log("Computing graph");

        // build graph
        const G = buildGraph(data._graph.ebunch);

        // loop through features and compute
        // impact tendency in each one (using the above graph)
        const attrs = [...data._geometry.features] // make a local copy of this
        
        const pathways = {}
        attrs.forEach( (feature) => {
            // Get properties for this feature
            // (these are all then accessible for graph computation)
            const local_vars = {...feature.properties,
                                ...variables, };
            
            // compute impact and impact pathways
            // N.B. per-node tendences will be added to local_vars!
            const impact = computeImpact(G, local_vars, seed_node, target_node);
            
            // store results in feature
            feature.properties = local_vars;
            //if (feature.properties.index=='8508e60ffffffff'){
            //    console.log(local_vars);
            //}

            //feature.properties['itend'] = impact.tendency;

            // and store impact pathways in a lookup that can
            // be used to find impact for any given GID
            pathways[feature.properties.index] = impact.pathways;
        });
        setMapAttrs(attrs); // store geometry for plotting
        setPathways(pathways); // store pathways for per-geometry explanation
        setGraph(G); // store graph for graph explanation
    }

    // build footer object
    let footer = <></>;
    if ((pathways!==null) & (selectedGID!==null)){
        if (pathways[selectedGID]){
            footer = <>
                    {showGraph ? 
                    (<><ImpactGraph impactPaths={pathways[selectedGID]} data={data} G={graph}/>
                    <hr/></>):(<></>) }
                    <ImpactPaths impactPaths={pathways[selectedGID]} data={data}/>
                    </>
        }
    }

    const map = <ImpactMap data={data} 
                    setSelectedGID={setSelectedGID} 
                    mapAttrs={mapAttrs}
                    setMapAttrs={setMapAttrs}
                    setVariables={setVariables}
                    mapKey={mapKey}
                    setMapKey={setMapKey} />

    
    return(<>{map} <hr/> {footer} </>);

    // return map once graph is computed
    /*return(<>
        <MapComponent data={data} 
                    setSelectedHID={setSelectedHID} 
                    setData={setData} />
        <hr/>
        <ImpactPaths impactPaths={selectedImpact} data={data}/>
        </>);*/
}
export default Model;