import React, { useState, useEffect, useRef } from 'react';
import { useMap } from 'react-leaflet';
import L from 'leaflet';
import './Sidebar.css';

function Sidebar({ styleProps, change_cmap, 
                   attributes, change_attr, 
                   selectedAttribute, variables, 
                   update_variables, data, tab, setTab, tabs}) {
    
    const cmapSelect = useRef(null);
    const attrSelect = useRef(null);
    const activeAttr = useRef({'Context':"Residential areas",
                                'Impact':'Biodiversity and Ecosystems',
                                'Tradeoffs':'ESGi'});
    const activeCmap = useRef({'Context':"Viridis",
                                'Impact':'Spectral',
                                'Tradeoffs':'Spectral'});
    
    // Toggle minimization state
    const [isMinimized, setIsMinimized] = useState(false); // State to track sidebar minimization
    const toggleSidebar = () => {
        setIsMinimized(!isMinimized);
    };

    // manage change in tab
    useEffect( ()=>{
        if (!cmapSelect.current) return;

        // change to selection for specified tab
        cmapSelect.current.value=activeCmap.current[tab];
        attrSelect.current.value=activeAttr.current[tab];
        change_cmap({target:cmapSelect.current});
        change_attr({target:attrSelect.current});
    }, [tab] );

    // build variables selector
    const var_items = [ ];
    Object.entries(variables).map(([key, value]) => {
        if ((key === "Priorities") && (tab !== "Tradeoffs")) return;
        if ((key === "Mine Properties") && (tab !== "Impact")) return;
        var_items.push(<div><p>{key}</p></div>)
        value.forEach( (n) => {
            const v = data[ data._lookup[n] ];
            if (v.values){ // categorical
                var_items.push(<div className="tooltip" key={`${n}1`}><div key={`${n}2`}>
                    <select id={n} name={n} key={n} style={{width:"10em"}} defaultValue={v.value}>
                        {Object.entries(v.values).map(([attr, attrval]) => {
                                return ( <option value={attrval*100} key={attr}>{attr}</option> );
                            })}
                    </select>
                    <label key={n+"_label"} htmlFor={n} style={{marginLeft:"1em"}}>{v.name}</label>
                    </div>
                    <div className="tooltiptext"> {v.tooltip} </div>
                </div> );

            } else {  // slider / numerical
                var_items.push(<div className="tooltip" key={`${n}1`}><div key={`${n}2`}>
                    <input type="range" id={n} name={n} key={n} min={100*v.range[0]} max={100*v.range[1]} style={{width:"10em"}}/>
                    <label key={n+"_label"} htmlFor={n} style={{marginLeft:"1em"}}>{v.name}</label>
                    </div>
                    <div className="tooltiptext"> {v.tooltip} </div>
                </div>
                );
            }
        });
    });

    // callback for "apply" button
    const apply = (evt) => {
        const out = {};
        Object.entries(variables).map(([key, value]) => {
            value.forEach( (n) => { 
                if (document.getElementById(n)){
                    out[ data._lookup[n] ] = parseFloat(document.getElementById(n).value) / 100;
                }
            } );
         } );
        update_variables(out); // pass on upwards
    }
    if (var_items.length > 0){ // apply button
        var_items.push( 
            <div className="tooltip" key="updatediv">
                <button id="updateData" type="button" style={{width:"100%"}} onClick={apply} key="updatebtn">Apply</button>
                <div className="tooltiptext" key="updatett"> Recompute model after changing properties above </div>
            </div>
        );
        var_items.push( <hr key="hr"/> );
    }
    // build attributes selector
    const attr_items= [ ];
    if (tab==='Context'){
        attributes.Indices.forEach( (v) => {
            attr_items.push( <option key={v} value={v}>{v}</option> );
        });
    } else {
        attributes.Impacts.forEach( (v) => {
            if (tab === data[ data._lookup[v] ].group ){
                attr_items.push( <option key={v} value={v}>{v}</option> );
            }            
        });
    }
    const attr = (
        <div className="tooltip">
        <select id="attribute-select" 
                ref={attrSelect}
                style={{width:"100%"}} 
                value={selectedAttribute}
                onChange={(e)=>{activeAttr.current[tab] = attrSelect.current.value; change_attr(e);}}>
            {attr_items}
        </select>
        <div className="tooltiptext">{`Choose the ${tab==="Context"?("model input"):("model output")} to display`}</div>
        </div> );
    
    // build colormap selector
    const cmap = (
        <div className="tooltip">
            <select id="property-select" 
                    ref={cmapSelect}
                    style={{width:"100%"}} 
                    value={styleProps.selectedScheme}
                    onChange={(e)=>{activeCmap.current[tab] = cmapSelect.current.value; change_cmap(e);}}>
            {Object.entries(styleProps.schemes).map(([key, value]) => {
                return( <option key={key} value={key}>{key}</option>); })}
            </select>
            <div className="tooltiptext"> Choose a colour scheme for the map </div>
        </div>);

    // build color legend item
    const legend = [];
    const s = 1; // step for legend bins
    for (let i = 0; i < styleProps.breaks.length-1; i+=1) {
      const from = styleProps.breaks[i];
      const to = styleProps.breaks[i + 1];
      const color = styleProps.color_func( (from + to) / 2).hex();
      let label = null;
      if (data[ data._lookup[selectedAttribute] ].range){
        if (i == 0){
            label = data[ data._lookup[selectedAttribute] ].range[0];
        } else if ((i+s) >= styleProps.breaks.length - 1) {
            label = data[ data._lookup[selectedAttribute] ].range[2];
        } else if (i == (styleProps.breaks.length)/2-1){
            label = data[ data._lookup[selectedAttribute] ].range[1];
        } else {
            label = '';
        }
      } else {
        if (i == 0){
            label = `< ${(to*100).toFixed(0)}%`;
        } else if ((i+s) >= styleProps.breaks.length - 1){
            label = `> ${(from*100).toFixed(0)}%`;
        } else {
            label = `${(from*100).toFixed(0)} - ${(to*100).toFixed(0)}%`;
        }
      }
      legend.push(
        <span key={from}>
            <br/><i key={color} style={{background:color, padding: '5px 25px'}}> 
            </i><span key={to} style={{padding: '0px 25px'}}>{label}</span>
        </span>
      );
    }
    
    const buttons = tabs.map( (t) => {
        if (t === 'Help'){ // help is special
            return <div className="tooltip"><button 
                    key={t}
                    className='button'
                    onClick={()=>{window.open('./#/help', '_blank')}}> {t} </button>
                    <div className="tooltiptext"> Open detailed help page </div>
                    </div>
        } else { // other buttons change "tab" property
            return <div className="tooltip"><button 
                        key={t}
                        onClick={()=>{setTab(t)}}
                        className={`button ${t===tab ? "active":""}`}> 
                    {t} </button>
                    <div className="tooltiptext"> View attributes related to mining {t.toLowerCase()} </div>
                    </div>
        }

    });

    // define "top text" to draw
    let top_text = "";
    if (tab === "Context"){
        top_text = "Explore social and environmental properties that (partially) determine a mine's positive and negative impacts.";
    } else if (tab === "Impact"){
        top_text = "Create a hypothetical new mine and indicate possible impacts, given different regions' social and environmental context.";
    } else if (tab === "Tradeoffs"){
        top_text = "Combine impacts given your priorities, to explore the tradeoffs underlying any new development.";
    }

    return (
        <div
          style={{
            position: 'absolute',
            textAlign: 'left',
            bottom: '10px',
            left: '10px',
            padding: '5px 10px',
            backgroundColor: 'rgba(255, 255, 255, 0.8)',
            borderRadius: '4px',
            boxShadow: '0 2px 5px rgba(0, 0, 0, 0.3)',
            fontSize: '14px',
            fontWeight: 'bold',
            zIndex: 1000, // Ensures overlay is above the map
            width: isMinimized ? 'auto' : '320px', // Adjust width for minimized state
            minHeight: '20px',
            minWidth: '20px',
          }}
        >
        {!isMinimized && buttons }
        <button
            onClick={toggleSidebar}
            style={{
            position: 'absolute',
            top: '2px',
            right: '2px',
            backgroundColor: 'transparent',
            border: 'none',
            cursor: 'pointer',
            fontSize: '16px',
            }}
        >
            {isMinimized ? '☰' : ' x '}
        </button>
        {!isMinimized && (
            <div key="d1">
                <p className="topText">{top_text}</p>
                
                <div>{var_items}</div>

                <b>Attribute</b>
                <div>
                    {attr}
                </div>

                <b>Colormap</b>
                <div>
                    {cmap}
                <div>
                    <b>Legend</b>
                    {legend}
                </div>
            </div>
        </div>
    )}
    </div>
    );
}

export default Sidebar;
