import React from 'react';

const Spinner = () => {
  const spinnerStyle = {
    margin: '100px auto',
    width: '50px',
    height: '50px',
    border: '8px solid rgba(0, 0, 0, 0.1)',
    borderTopColor: '#000',
    borderRadius: '50%',
    animation: 'spin 1s linear infinite',
  };

  return <div style={spinnerStyle}></div>;
};

export default Spinner;
