import React from 'react';

function ImpactPaths({ impactPaths, data }) {
  
  // if null, return nothing
  if (!impactPaths){
    return <></>
  }

  //console.log(impactPaths);
  
  // JSX for total impact
  const totalImpact = (
    <div style={{ paddingLeft: '2em', paddingBottom: '1em', textAlign: 'left'  }}>
        <p><b>Combined (priority weighted) impact tendency: 
            <span style={{paddingLeft: '0.2em', color: impactPaths.total > 0 ? 'green' : 'red' }}>
                {(impactPaths.total*100).toFixed(2)}
            </span>
        </b></p>
    </div>
  )
  
  // JSX for positive pathways
  const positivePathways = impactPaths.positive.length > 0 && (
    <div style={{ paddingLeft: '2em', paddingBottom: '1em', textAlign: 'left'  }}>
      <b>Possible opportunities (positive impact tendency):</b>
      {impactPaths.positive.map((p, index) => (
        <div key={index}>
          {p.path.map((e, idx) => {
            const start = data[e[0]].name;
            const end = data[e[1]].name;
            const endurl = data[e[1]].source;
            const dNdP = e[2];
            const arrowColor = dNdP > 0 ? 'green' : 'red';
            return (
                <span key={idx} style={{ color: arrowColor }}>
                    {idx===0 ? <></> : <b>&#8594;</b>  }
                    <a href={endurl} target="_blank" style={{ color: arrowColor }}>{end}</a>
                </span>
            );
          })}
        <span style={{ paddingLeft: '1em'}}>
            (tendency ≈ <span style={{color: p.tendency > 0 ? 'green' : 'red' }}> {(p.tendency*100).toFixed(2)} </span>)
        </span>
        </div>
      ))}
    </div>
  );

  // JSX for negative pathways
  const negativePathways = impactPaths.negative.length > 0 && (
    <div style={{ paddingLeft: '2em', paddingBottom: '1em', textAlign: 'left'  }}>
      <b>Possible conflicts (negative impact tendency):</b>
      {
      impactPaths.negative.map((p, index) => (
        <div key={index}>
          {p.path.map((e, idx) => {
            const start = data[e[0]].name;
            const end = data[e[1]].name;
            const endurl = data[e[1]].source;
            const dNdP = e[2];
            const arrowColor = dNdP > 0 ? 'green' : 'red';
            return (
                <span key={idx} style={{ color: arrowColor }}>
                    {idx===0 ? <></> : <b>&#8594;</b>  }
                    <a href={endurl} target="_blank" style={{ color: arrowColor }}>{end}</a>
                </span>
            );
          })}
          <span style={{ paddingLeft: '1em'}}>
                (tendency ≈ <span style={{color: p.tendency > 0 ? 'green' : 'red' }}> {(p.tendency*100).toFixed(2)} </span>)
          </span>
        </div>
      ))}
    </div>
  );
  
  // JSX for unknown pathways
  const unknownPathways = impactPaths.unknown.length > 0 && (
    <div style={{ paddingLeft: '2em', paddingBottom: '1em', textAlign: 'left'  }}>
      <b>Unresolvable impacts (lack of data):</b>
      {
      impactPaths.unknown.map((p, index) => (
        <div key={index}>
          {p.path.map((e, idx) => {
            const start = data[e[0]].name;
            const end = data[e[1]].name;
            const endurl = data[e[1]].source;
            const dNdP = e[2];
            const arrowColor = dNdP > 0 ? 'green' : 'red';
            return (
                <span key={idx} style={{ color: arrowColor }}>
                    {idx===0 ? <></> : <b>&#8594;</b>  }
                    <a href={endurl} target="_blank" style={{ color: arrowColor }}>{end}</a>
                </span>
            );
          })}
        </div>
      ))}
    </div>
  );

  // Render the full JSX output
  return (
    <div>
      {totalImpact}
      {positivePathways}
      {negativePathways}
      {unknownPathways}
    </div>
  );
}

export default ImpactPaths;
